<template>
    <vx-card>
		<div class="flex flex-wrap-reverse items-center">
			<h3 class="mr-auto">Vehicles</h3>
			<!-- ADD NEW -->
			<div class="btn-add-new p-3 mr-4 rounded-lg cursor-pointer flex items-center justify-center text-lg font-medium text-base text-primary border border-solid border-primary">
				<feather-icon icon="PlusIcon" svgClasses="h-4 w-4" />
				<span class="ml-2 text-base text-primary">Add New</span>
			</div>
        </div>

        <div class="mt-5">
            <vs-tabs>
                <vs-tab label="All Vehicles">
					<vs-table :data="users" pagination max-items="10" search multiple :class="{'selection': !selection}" v-model="selected">
						<!-- <template slot="header">
							<vs-checkbox size="small" v-model="selection" class="pl-3">&nbsp;Selection</vs-checkbox>
						</template> -->
						<template slot="thead">
							<vs-th v-for="(header, index) in headers" :key="index">
								<span class="uppercase mr-2">{{ header }}</span>
							</vs-th>
						</template>
						<template slot-scope="{data}">
							<vs-tr :data="tr" :key="indextr" v-for="(tr, indextr) in data" @click.native="openPopup">
								<vs-td :data="data[indextr].id">
									{{data[indextr].id}}
								</vs-td>
								<vs-td :data="data[indextr].sylectus_id">
									{{data[indextr].sylectus_id}}
								</vs-td>
								<vs-td :data="data[indextr].drivers">
									<vs-col vs-type="flex" vs-align="center">
										<vs-avatar src="https://avatars2.githubusercontent.com/u/31676496?s=460&v=4"/>
										<span class="ml-2">{{data[indextr].drivers}}<br><span class="summary">#12345 NSurname</span></span>
									</vs-col>
								</vs-td>
								<vs-td :data="data[indextr].truck_type">
									{{data[indextr].truck_type}}
								</vs-td>
								<vs-td :data="data[indextr].size">
									{{data[indextr].size}}
								</vs-td>
								<vs-td :data="data[indextr].status">
									<vs-chip>
										<div class="h-2 w-2 rounded-full mr-2" :class="'bg-' + data[indextr].status.color"></div>
										<span>{{data[indextr].status.label}}</span>
									</vs-chip>
								</vs-td>
								<vs-td :data="data[indextr].available_city">
									{{data[indextr].available_city}}
								</vs-td>
								<vs-td :data="data[indextr].available_date">
									{{data[indextr].available_date}}
								</vs-td>
							</vs-tr>
						</template>
					</vs-table>
                </vs-tab>
            </vs-tabs>

			<!-- Popup -->
			<vs-popup title="Drivers" class="holamundo" :active.sync="activePopup" button-close-hidden>
				<div class="px-3">
					<h3 class="title">Vehicle #3163</h3>
					<span class="status">Added by SYLECTUS</span>
				</div>
				<div class="p-3">
					<div class="vx-row">
						<div class="vx-col sm:w-1/2 w-full mb-3">
							<vs-select class="w-full" label="Driver #1:" placeholder="Select an Address Type">
								<vs-select-item :key="index" :value="item.value" :text="item.text" v-for="(item, index) in options" />
							</vs-select>
						</div>
						<div class="vx-col sm:w-1/2 w-full mb-3">
							<vs-input class="w-full" label="Vehicle make:" />
						</div>
					</div>
					<div class="vx-row">
						<div class="vx-col sm:w-1/2 w-full mb-3">
							<vs-select class="w-full" label="Driver #2:" placeholder="Select an Address Type">
								<vs-select-item :key="index" :value="item.value" :text="item.text" v-for="(item, index) in options" />
							</vs-select>
						</div>
						<div class="vx-col sm:w-1/2 w-full mb-3">
							<vs-input class="w-full" label="Vehicle model:" />
						</div>
					</div>
					<div class="vx-row">
						<div class="vx-col sm:w-1/2 w-full mb-3">
							<vs-select class="w-full" label="Owner:" placeholder="Select an Address Type">
								<vs-select-item :key="index" :value="item.value" :text="item.text" v-for="(item, index) in options" />
							</vs-select>
						</div>
						<div class="vx-col sm:w-1/2 w-full mb-3">
							<vs-input class="w-full" label="Vehicle year:" />
						</div>
					</div>
					<div class="vx-row">
						<div class="vx-col sm:w-1/2 w-full mb-3">
							<vs-select class="w-full" label="Vehicle type:" placeholder="Select an Address Type">
								<vs-select-item :key="index" :value="item.value" :text="item.text" v-for="(item, index) in options" />
							</vs-select>
						</div>
						<div class="vx-col sm:w-1/2 w-full mb-3">
							<vs-input class="w-full" label="VIN:" />
						</div>
					</div>
					<div class="vx-row">
						<div class="vx-col sm:w-1/2 w-full mb-3">
							<vs-input class="w-full" label="Payload:" />
						</div>
						<div class="vx-col sm:w-1/2 w-full mb-3">
							<vs-select class="w-full" label="License state:" placeholder="Select an Address Type">
								<vs-select-item :key="index" :value="item.value" :text="item.text" v-for="(item, index) in options" />
							</vs-select>
						</div>
					</div>
					<div class="vx-row">
						<div class="vx-col sm:w-1/2 w-full mb-3">
							<vs-input class="w-full" label="Box dims:" />
						</div>
						<div class="vx-col sm:w-1/2 w-full mb-3">
							<label>License exp date:</label>
							<datepicker placeholder="Select Date"></datepicker>
						</div>
					</div>
					<div class="vx-row">
						<div class="vx-col sm:w-1/2 w-full mb-3">
							<vs-select class="w-full" label="Status:" placeholder="Select an Address Type">
								<vs-select-item :key="index" :value="item.value" :text="item.text" v-for="(item, index) in options" />
							</vs-select>
						</div>
						<div class="vx-col sm:w-1/2 w-full mb-3">
							<label>Insurance exp:</label>
							<datepicker placeholder="Select Date"></datepicker>
						</div>
					</div>
					<div class="vx-row">
						<div class="vx-col sm:w-1/2 w-full mb-3">
							<vs-input class="w-full" label="Available city:" />
						</div>
						<div class="vx-col sm:w-1/2 w-full mb-3">
							<vs-select class="w-full" label="Availability note:" placeholder="Select an Address Type">
								<vs-select-item :key="index" :value="item.value" :text="item.text" v-for="(item, index) in options" />
							</vs-select>
						</div>
					</div>
					<div class="vx-row">
						<div class="vx-col sm:w-1/2 w-full mb-3">
							<vs-input class="w-full" label="Available date:" />
						</div>
						<div class="vx-col sm:w-1/2 w-full flex items-center mb-2">
							<vs-checkbox>&nbsp;Update data from Sylectus</vs-checkbox>
						</div>
					</div>
					<div class="vx-row">
						<div class="vx-col sm:w-1/2 w-full mb-3">
							<vs-input class="w-full" label="Team:" />
						</div>
						<div class="vx-col sm:w-1/2 w-full mb-3">
							<div class="vx-col w-full h-full flex flex-wrap justify-end items-end">
								<vs-button color="#afafaf" type="filled" class="mr-4" @click="activePopup = false">CLOSE</vs-button>
								<vs-button color="success" type="filled">SAVE</vs-button>
							</div>
						</div>
					</div>
				</div>
			</vs-popup>
        </div>
    </vx-card>
</template>

